// 
const whiteList = [
	// '/pages/index/index',
]

function hasPermission (url) {
	let islogin = uni.getStorageSync('isLogin') // token,isLogin
	islogin = Boolean(Number(islogin)) // 
    // 
    if(whiteList.indexOf(url) !== -1 || islogin) {
		console.log('')
        return true
    }
	console.log('')
    return false
}

uni.addInterceptor('navigateTo', {
    // , invoke
    invoke (e) {
		const token = uni.getStorageSync('token')
        if(!token && !hasPermission(e.url)){
            uni.reLaunch({
                url: '/pages/login/login'
            })
            return false
        }
        return true
    },
    success (e) {
		
    }
})

uni.addInterceptor('switchTab', {
    // tabbar
    invoke (e) {
		const token = uni.getStorageSync('token')
        if(!token && !hasPermission(e.url)){
            uni.reLaunch({
                url: '/pages/login/login'
            })
            return false
        }
        return true
    },
    success (e) {
		
    }
})
